
.fn-m-job-select{
	.title{
		padding:10*@rex 18*@rex;
	}
	.list{
		background:#fff;

		li{
			padding:15*@rex 20*@rex;
			overflow:hidden;
			border-bottom:1*@rex solid #e5e5e5;

			&:last-child{
				border-bottom:none;
			}

			&.cur{
				.iconfont{display:block;}
			}

			.txt{float:left}
			.iconfont{float:right;display:none;color:@main-color;}

		}
	}
}